import React, { useState, Fragment } from "react";
import { Formik, Field } from "formik";
import * as yup from "yup";
import { classList } from "@utils";
import { nanoid } from "nanoid";
import CustomMultiSelect from "./CustomMultiSelect";
import { applySlot } from "app/reducers/actions/ClassroomActions";
import Swal from "sweetalert2";
import { Button, Spinner, Modal } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Select from "react-select";
import dayjs from "dayjs";
import { generateTimeSlots } from "./functions/generateTime";
import resource from "@fullcalendar/resource";
const basicFormSchema = yup.object().shape({
  resourceId: yup.string().required("Room is required"),
  startDate: yup.string().required("Start Date is required"),
  endDate: yup.string().required("End Date is required"),
  startTime: yup.string().required("Start Time is required"),
  subject: yup.string().required("Subject is required"),
  startTime: yup.string().required("Start Time is required"),
  endTime: yup.string().required("Eend Time is required"),
  course: yup.string().required("Course is required"),
  yearLevel: yup.string().required("Year Level is required"),
  days: yup.array().required("Days is required"),
});
const CreateSlot = ({
  handleClose,
  token,
  dispatch,
  show,
  teachers,
  classrooms,
  courses,
  user,
  handleShowCreateSlot,
  ...props
}) => {
  const [loading, setLoading] = useState(false);
  const initialState = {
    resourceId: "",
    startDate: "",
    endDate: "",
    startTime: "",
    endTime: "",
    teacher: "",
    subject: "",
    course: "",
    yearLevel: "",
    days: "",
  };
  const teacher = teachers.find((teacher) => teacher.id === user.uid);

  const handleSubmit = async (values) => {
    const {
      startDate,
      endDate,
      endTime,
      startTime,
      resourceId,
      course,
      subject,
      yearLevel,
      days,
    } = values;
    let start = dayjs(startDate);
    let end = dayjs(endDate);
    let daysOfWeek = [];
    for (
      let currentDate = start;
      currentDate <= end;
      currentDate = dayjs(currentDate).add(1, "day")
    ) {
      let dayOfWeek = dayjs(currentDate).format("dddd");
      daysOfWeek.push(dayOfWeek);
    }
    let conflict = days.every((value) => daysOfWeek.includes(value.label));
    console.log(conflict);
    console.log(days);
    if (conflict === false) {
      Swal.fire(
        "Please make sure that your selected dates has correct corresponding days schedule"
      );
    } else if (
      endTime.value < startTime.value ||
      startTime.value === endTime.value
    ) {
      Swal.fire("Something is wrong with your time combination");
    } else if (startDate === endDate || endDate < startDate) {
      Swal.fire("Date selection error please check the selected date");
    } else {
      const daysList = [];
      days.map((day) => daysList.push(day.label));
      const classroomSLot = {
        id: nanoid(),
        resourceId: resourceId.value,
        startDate: new Date(startDate).getTime(),
        endDate: new Date(endDate).getTime(),
        startTime: startTime.value,
        endTime: endTime.value,
        subject: subject,
        teacher: user.uid,
        yearLevel: yearLevel,
        course: course,
        isApproved: false,
        days: daysList,
        studentsId: [],
        created: Date.now(),
      };
      setLoading(true);
      await applySlot(classroomSLot, token)(dispatch);
      setLoading(false);
      handleClose();
    }
  };
  const classroomOptions = classrooms.map((room) => {
    return {
      label: room.title,
      value: room.id,
    };
  });
  const timeOptions = generateTimeSlots(7, 21).map((time) => time);
  const years = ["1st Year", "2nd Year", "3rd Year", "4th Year", "5th Year"];
  const days = [
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
    "Sunday",
  ];
  const formatTimestamp = (timestamp) => {
    return dayjs(timestamp).format("YYYY-MM-DD");
  };

  const daysOptions = days.map((day, idx) => {
    return {
      label: day,
      value: idx,
    };
  });
  const courseOptions =
    courses &&
    courses.map((course) => {
      return {
        value: course.courseName,
        label: course.courseName,
      };
    });
  const subjectsOption =
    teacher &&
    teacher.subjects.map((subject) => {
      return {
        value: subject.value,
        label: subject.label,
      };
    });
  const yearLevelOptions = years.map((year) => {
    return {
      value: year,
      label: year,
    };
  });
  return (
    <Fragment>
      <Modal show={show} size="lg" onHide={handleShowCreateSlot} {...props}>
        <Modal.Header closeButton>
          <Modal.Title>
            <i className="i-Pen-2"></i> Apply Schedule
          </Modal.Title>
        </Modal.Header>
        <Formik
          initialValues={initialState}
          validationSchema={basicFormSchema}
          onSubmit={handleSubmit}
        >
          {({
            values,
            errors,
            touched,
            handleSubmit,
            isSubmitting,
            handleChange,
            setFieldValue,
          }) => {
            return (
              <form
                className="needs-validation"
                onSubmit={handleSubmit}
                noValidate
              >
                <Modal.Body>
                  {teacher && (
                    <div className="card-body">
                      <span>
                        Please fill all the required (
                        <span className="text-danger">*</span>) fields
                      </span>
                      <div className="form-row">
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field":
                              errors.resourceId && touched.resourceId,
                          })}
                        >
                          <label
                            htmlFor="resourceId"
                            className="ul-form__label"
                          >
                            Room <span className="text-danger">*</span>
                          </label>
                          <Field name="resourceId">
                            {({ field, form }) => (
                              <Select
                                options={classroomOptions}
                                value={classroomOptions.find(
                                  (o) => o.value === values.resourceId
                                )}
                                onChange={(selectedOption) => {
                                  form.setFieldValue(
                                    "resourceId",
                                    selectedOption
                                  );
                                }}
                              />
                            )}
                          </Field>
                          <div className="invalid-feedback">
                            {errors.resourceId}
                          </div>
                        </div>
                        <div className="form-group col-md-6"></div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field":
                              errors.startDate && touched.startDate,
                          })}
                        >
                          <label
                            htmlFor="startDate"
                            className="ul-form__label mr-2"
                          >
                            Start Date <span className="text-danger">*</span>
                          </label>
                          <div className="customDatePickerWidth">
                            <Field name="startDate">
                              {({ field, form }) => (
                                <DatePicker
                                  dateFormat="yyyy/MM/dd"
                                  className="form-control "
                                  placeholderText="Select Date"
                                  selected={values.startDate}
                                  onChange={(date) => {
                                    console.log(date);
                                    form.setFieldValue("startDate", date);
                                  }}
                                />
                              )}
                            </Field>
                          </div>
                          <div className="invalid-feedback">
                            {errors.startDate}
                          </div>
                        </div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field": errors.endTime && touched.endTime,
                          })}
                        >
                          <label
                            htmlFor="endDate"
                            className="ul-form__label mr-2"
                          >
                            End Date <span className="text-danger">*</span>
                          </label>
                          <div className="customDatePickerWidth">
                            <Field name="endDate">
                              {({ field, form }) => (
                                <DatePicker
                                  className="form-control"
                                  dateFormat="yyyy/MM/dd"
                                  placeholderText="Select Date"
                                  selected={values.endDate}
                                  onChange={(date) => {
                                    console.log(date);
                                    form.setFieldValue("endDate", date);
                                  }}
                                />
                              )}
                            </Field>
                          </div>
                          <div className="invalid-feedback">
                            {errors.endDate}
                          </div>
                        </div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field":
                              errors.startTime && touched.startTime,
                          })}
                        >
                          <label htmlFor="startTime" className="ul-form__label">
                            Start Time <span className="text-danger">*</span>
                          </label>
                          <Field name="startTime">
                            {({ field, form }) => (
                              <Select
                                options={timeOptions}
                                value={timeOptions.find(
                                  (o) => o.value === values.startTime
                                )}
                                onChange={(selectedOption) => {
                                  form.setFieldValue(
                                    "startTime",
                                    selectedOption
                                  );
                                }}
                              />
                            )}
                          </Field>
                          <div className="invalid-feedback">
                            Start Time is required
                          </div>
                        </div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field": errors.endTime && touched.endTime,
                          })}
                        >
                          <label htmlFor="endTime" className="ul-form__label">
                            End Time
                          </label>
                          <Field name="endTime">
                            {({ field, form }) => (
                              <Select
                                options={timeOptions}
                                value={timeOptions.find(
                                  (o) => o.value === values.endTime
                                )}
                                onChange={(selectedOption) => {
                                  form.setFieldValue("endTime", selectedOption);
                                }}
                              />
                            )}
                          </Field>
                          <div className="invalid-feedback">
                            End Time is required
                          </div>
                        </div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field": errors.subject && touched.subject,
                          })}
                        >
                          <label htmlFor="subject" className="ul-form__label">
                            Subject <span className="text-danger">*</span>
                          </label>
                          <Field name="subject">
                            {({ field, form }) => (
                              <Select
                                options={subjectsOption}
                                value={subjectsOption.find(
                                  (o) => o.value === values.subject
                                )}
                                onChange={(selectedOption) => {
                                  form.setFieldValue("subject", selectedOption);
                                }}
                              />
                            )}
                          </Field>
                          <div className="invalid-feedback">
                            Subject is required
                          </div>
                        </div>

                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field": errors.course && touched.course,
                          })}
                        >
                          <label htmlFor="course" className="ul-form__label">
                            Course <span className="text-danger">*</span>
                          </label>
                          <Field name="course">
                            {({ field, form }) => (
                              <Select
                                options={courseOptions}
                                value={courseOptions.find(
                                  (o) => o.value === values.course
                                )}
                                onChange={(selectedOption) => {
                                  form.setFieldValue("course", selectedOption);
                                }}
                              />
                            )}
                          </Field>
                          <div className="invalid-feedback">
                            Course is required
                          </div>
                        </div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field":
                              errors.yearLevel && touched.yearLevel,
                          })}
                        >
                          <label htmlFor="yearLevel" className="ul-form__label">
                            Year Level <span className="text-danger">*</span>
                          </label>
                          <Field name="yearLevel">
                            {({ field, form }) => (
                              <Select
                                options={yearLevelOptions}
                                value={yearLevelOptions.find(
                                  (o) => o.value === values.yearLevel
                                )}
                                onChange={(selectedOption) => {
                                  form.setFieldValue(
                                    "yearLevel",
                                    selectedOption
                                  );
                                }}
                              />
                            )}
                          </Field>
                          <div className="invalid-feedback">
                            Year Level is required
                          </div>
                        </div>
                        <div
                          className={classList({
                            "form-group col-md-6": true,
                            "invalid-field": errors.day && touched.day,
                          })}
                        >
                          <label htmlFor="days" className="ul-form__label">
                            Days <span className="text-danger">*</span>
                          </label>
                          <Field
                            name="days"
                            component={CustomMultiSelect}
                            options={daysOptions}
                            value={values.days}
                            required
                          />
                          <div className="invalid-feedback">
                            Days is required
                          </div>
                        </div>
                      </div>
                      <div className="custom-separator"></div>
                    </div>
                  )}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="danger" onClick={handleShowCreateSlot}>
                    Cancel
                  </Button>
                  <Button disabled={loading} variant="info" type="submit">
                    {loading && (
                      <Spinner
                        as="span"
                        variant="light"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                        animation="border"
                        className="mr-1"
                      />
                    )}
                    Submit
                  </Button>
                </Modal.Footer>
              </form>
            );
          }}
        </Formik>
      </Modal>
    </Fragment>
  );
};

export default CreateSlot;
