import React, { useState, Fragment } from "react";
import { Modal, Button, Spinner } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
import { Formik } from "formik";
import * as yup from "yup";
import { classList } from "@utils";
import Swal from "sweetalert2";
import { addClassroom } from "app/reducers/actions/ClassroomActions";
import { nanoid } from "nanoid";
const basicFormSchema = yup.object().shape({
  title: yup.string().required("Room Name is empty"),
});

const AddClassroomForm = ({ handleClose, token, dispatch, show, ...props }) => {
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const getRandomColor = () => {
    let letters = "0123456789ABCDEF";
    let color = "#";
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  };
  const handleSubmit = async (values) => {
    const { title } = values;
    const classroom = {
      id: nanoid(),
      title: title,
      eventColor: getRandomColor(),
      created: Date.now(),
    };
    setLoading(true);
    await addClassroom(classroom, token)(dispatch);
    setLoading(false);
    handleClose();
  };
  return (
    <Fragment>
      <Modal show={show} onHide={handleClose} {...props}>
        <Formik
          initialValues={{
            title: "",
          }}
          validationSchema={basicFormSchema}
          onSubmit={handleSubmit}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            isSubmitting,
          }) => {
            return (
              <form
                className="needs-validation"
                onSubmit={handleSubmit}
                noValidate
              >
                <Modal.Header closeButton>
                  <Modal.Title>
                    <i className="i-University"></i> Add Classroom
                  </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <span>
                    Please fill all the required (
                    <span className="text-danger">*</span>) fields
                  </span>

                  <div className="form-row mt-2">
                    <div className="form-group col-md-12">
                      <div
                        className={classList({
                          "mx-0": true,
                          "valid-field":
                            !errors.oldPassword && touched.oldPassword,
                          "invalid-field":
                            errors.oldPassword && touched.oldPassword,
                        })}
                      >
                        <label htmlFor="title" className="font-weight-bold">
                          Room name{" "}
                          <span className="text-danger font-weight-bold">
                            *
                          </span>
                        </label>
                        <input
                          type="text"
                          className="form-control"
                          id="title"
                          placeholder="Enter Room Name"
                          name="title"
                          value={values.title}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          required
                        />
                        <div className="invalid-feedback">{errors.title}</div>
                      </div>
                    </div>
                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="danger" onClick={handleClose}>
                    Cancel
                  </Button>
                  <Button disabled={loading} variant="info" type="submit">
                    {loading && (
                      <Spinner
                        as="span"
                        variant="light"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                        animation="border"
                        className="mr-1"
                      />
                    )}
                    Submit
                  </Button>
                </Modal.Footer>
              </form>
            );
          }}
        </Formik>
      </Modal>
    </Fragment>
  );
};

export default AddClassroomForm;
