import React, { useState, useRef, useContext, useEffect } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction";
import resourceTimelinePlugin from "@fullcalendar/resource-timeline";
import "./index.css";
import "react-datepicker/dist/react-datepicker.css";
import { Suspense } from "react";
import { Loading } from "@gull";
import { useHistory } from "react-router-dom";
import ScheduleInfo from "./ScheduleInfo";

const CalendarSchedule = ({
  calendarEvents,
  user,
  classrooms,
  classroomSlots,
  courses,
  userId,
  token,
  dispatch,
  teachers,
  resource,
  date,
  setDate,
}) => {
  const history = useHistory();
  const [eventData, setEventData] = useState({});
  const [showScheduleInfo, setShowScheduleInfo] = useState(false);
  const fullCalendarRef = useRef(null);

  useEffect(() => {
    if (fullCalendarRef.current) {
      fullCalendarRef.current.calendar.gotoDate(date);
      console.log(date);
    }
  }, [date]);

  const handleCloseScheduleModal = () => {
    setShowScheduleInfo(false);
  };

  const handleEventClick = (info) => {
    setEventData(info.event.extendedProps.scheduleInformation);
    setShowScheduleInfo(true);
  };

  const handleApplySlotForm = () => {
    history.push("/classrooms/applyslot");
  };
  return (
    <div className="demo-app">
      <div className="container">
        <div className="demo-app-main">
          <FullCalendar
            schedulerLicenseKey={"CC-Attribution-NonCommercial-NoDerivatives"}
            ref={fullCalendarRef}
            plugins={[
              dayGridPlugin,
              timeGridPlugin,
              interactionPlugin,
              resourceTimelinePlugin,
            ]}
            headerToolbar={{
              left: "prev,next today",
              center: "title",
              right: "timeGridDay,timeGridWeek,dayGridMonth",
            }}
            allDaySlot={false}
            slotEventOverlap={false}
            eventDisplay="list-item"
            slotMinTime="07:00:00"
            slotMaxTime="21:00:00"
            slotDuration={"1:00:00"}
            dayHeaders={true}
            events={calendarEvents}
            resourceAreaWidth={80}
            resourceAreaHeaderContent="Rooms"
            initialView="timeGridDay"
            resources={classrooms}
            selectMirror
            dayMaxEvents
            // eventMaxStack={true}
            eventClick={handleEventClick}
          />
        </div>

        <Suspense fallback={<Loading></Loading>}>
          {showScheduleInfo && (
            <ScheduleInfo
              handleCloseScheduleModal={handleCloseScheduleModal}
              showScheduleInfo={showScheduleInfo}
              data={eventData}
              teachers={teachers}
            />
          )}
        </Suspense>

        <div></div>
      </div>
    </div>
  );
};

export default CalendarSchedule;
