import React, { useState, useRef, useContext, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import AppContext from "app/appContext";
import { Suspense } from "react";
import { Loading } from "@gull";
import { useHistory } from "react-router-dom";
import dayjs from "dayjs";
import { format } from "date-fns";
import Select from "react-select";
import CalendarSchedule from "./FullCalendar";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { Breadcrumb } from "@gull";
import AddClassroomForm from "./AddClassroomForm";
import ScheduleListView from "./ScheduleListView";
import CreateSlot from "./CreateSlot";

const ScheduleList = () => {
  const {
    classrooms,
    courses,
    teachers,
    user,
    token,
    dispatch,
    classroomSlots,
    subjects,
  } = useContext(AppContext);
  const [selectedRoom, setSelectedRoom] = useState();
  const [showCreateSlot, setShowCreateSlot] = useState(false);
  const [selectedTeacher, setSelectedTeacher] = useState("");
  const [selectedSubject, setSelectedSubject] = useState("");
  const [calendarEvents, setCalendarEvents] = useState();
  const [listSlots, setListSlots] = useState();
  const [date, setDate] = useState(new Date());
  const [showModal, setShowModal] = useState(false);
  const [selectedFilter, setSelectedFilter] = useState(
    user && user.role === "_admin" ? "rooms" : "schedule"
  );
  useEffect(() => {
    if (user) {
      if (user.role === "_admin") {
        setCalendarEvents(generateEvents(classroomSlots));
        setListSlots(classroomSlots);
      }
      if (user.role === "_teacher") {
        const filtered = classroomSlots.filter(
          (slot) => slot.teacher === user.uid
        );
        setCalendarEvents(generateEvents(filtered));
        setListSlots(filtered);
      }
    }
  }, []);
  const [cardView, setCardView] = useState(true);
  const [listView, setListView] = useState(false);
  const [loading, setLoading] = useState(false);
  const handleCardView = () => {
    setListView(false);
    setCardView(true);
  };
  console.log(calendarEvents);
  const handleShowCreateSlot = () => {
    setShowCreateSlot(false);
  };
  const handleApplySlotForm = () => {
    setShowCreateSlot(true);
  };
  const handleListView = () => {
    setCardView(false);
    setListView(true);
  };
  const formatTimestamp = (timestamp) => {
    return dayjs(timestamp).format("YYYY-MM-DD");
  };
  const handleSubjectChange = (event) => {
    setSelectedSubject(event.target.value);
  };
  const handleTeacherChange = (event) => {
    setSelectedTeacher(event.target.value);
  };
  const handleRoomChange = (event) => {
    setSelectedRoom(event.target.value);
  };
  const handleFilterChange = (event) => {
    setSelectedFilter(event.target.value);
  };
  const handleClose = () => {
    setShowModal(false);
  };
  const handleCalendarChange = (selectedDate, event) => {
    setDate(selectedDate);
    console.log(event);
  };
  const handleAddClassroomForm = () => {
    setShowModal(true);
  };
  const handleFilter = (filterBy) => {
    setLoading(true);
    setTimeout(() => {
      if (filterBy === "rooms") {
        const filtered = classroomSlots.filter(
          (slot) => slot.resourceId === selectedRoom
        );
        setCalendarEvents(generateEvents(filtered));
        setListSlots(filtered);
      }
      if (filterBy === "schedule") {
        const filtered = classroomSlots.filter(
          (slot) => slot.teacher === user.uid
        );
        setCalendarEvents(generateEvents(filtered));
        setListSlots(filtered);
      }
      if (filterBy === "teachers") {
        const filtered = classroomSlots.filter(
          (slot) => slot.teacher === selectedTeacher
        );
        setCalendarEvents(generateEvents(filtered));
        setListSlots(filtered);
      }
      if (filterBy === "subjects") {
        const filtered = classroomSlots.filter(
          (slot) => slot.subject.value === selectedSubject
        );
        setCalendarEvents(generateEvents(filtered));
        setListSlots(filtered);
      }
    }, 1000);
    setLoading(false);
  };
  function generateEvents(filteredData) {
    let events = [];
    filteredData.forEach((item) => {
      let start = dayjs(formatTimestamp(item.startDate));
      let end = dayjs(formatTimestamp(item.endDate));
      for (
        let currentDate = start;
        currentDate <= end;
        currentDate = dayjs(currentDate).add(1, "day")
      ) {
        let dayOfWeek = dayjs(currentDate).format("dddd");
        if (item.days.includes(dayOfWeek)) {
          events.push({
            id: item.id,
            title: `${item.subject.label} - ${
              teachers.find((teacher) => teacher.id === item.teacher)
                ? teachers.find((teacher) => teacher.id === item.teacher)
                    .teacherName
                : "-"
            }`,
            start: currentDate
              .add(item.startTime, "hour")
              .format("YYYY-MM-DDTHH:mm:ssZ"),
            end: currentDate
              .add(item.endTime, "hour")
              .format("YYYY-MM-DDTHH:mm:ssZ"),
            resourceId: item.resourceId,
            scheduleInformation: {
              startTime: format(new Date(0, 0, 0, item.startTime), "h:mm aa"),
              endTime: format(new Date(0, 0, 0, item.endTime), "h:mm aa"),
              subject: item.subject,
              course: item.course,
              yearLevel: item.yearLevel,
              days: item.days,
              isApproved: item.isApproved,
              teacher: item.teacher,
              resourceId: item.resourceId,
            },
          });
        }
      }
    });
    return events;
  }
  return (
    <div>
      <Breadcrumb routeSegments={[{ name: "Schedule List" }]} />
      <div className="row">
        <div className="col-md-7">
          <button onClick={handleAddClassroomForm} className="btn btn-primary">
            <i className="nav-icon i-Add "></i> Add Room
          </button>
        </div>
        <div className="col-md-5">
          <div className="text-right form-group">
            <button onClick={handleApplySlotForm} className="btn btn-info">
              <i className="i-Pen-2"></i> Apply Schedule
            </button>
          </div>
        </div>
      </div>
      <div className="d-flex flex-row justify-content-between align-items-center mt-4">
        <div className="text-left col-md-4">
          <div className="input-group">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Filter By:
              </span>
            </div>
            {user && user.role === "_admin" && (
              <select
                class="custom-select"
                name="filter"
                defaultValue="rooms"
                onChange={handleFilterChange}
              >
                <option value="rooms">Rooms</option>
                <option value="teachers">Teachers</option>
                <option value="subjects">Subjects</option>
              </select>
            )}
            {user && user.role === "_teacher" && (
              <select
                class="custom-select"
                name="filter"
                defaultValue="schedule"
                onChange={handleFilterChange}
              >
                <option value="schedule">My Schedule</option>
                <option value="rooms">Rooms</option>
              </select>
            )}
            {selectedFilter === "rooms" && (
              <select
                name="rooms"
                id="rooms"
                value={selectedRoom}
                class="custom-select"
                onChange={handleRoomChange}
              >
                <option defaultValue="">Select</option>
                {classrooms.map((classroom) => (
                  <option value={classroom.id}>{classroom.title}</option>
                ))}
              </select>
            )}
            {selectedFilter === "teachers" &&
              user &&
              user.role ===
                "_admin"(
                  <select
                    name="teachers"
                    id="teachers"
                    class="custom-select"
                    value={selectedTeacher}
                    defaultValue={selectedTeacher}
                    onChange={handleTeacherChange}
                  >
                    <option value="">Select</option>
                    {teachers.map((teacher) => (
                      <option value={teacher.id}>{teacher.teacherName}</option>
                    ))}
                  </select>
                )}
            {selectedFilter === "subjects" &&
              user &&
              user.role ===
                "_admin"(
                  <select
                    name="subjects"
                    id="subjects"
                    defaultValue={selectedSubject}
                    value={selectedSubject}
                    class="custom-select"
                    onChange={handleSubjectChange}
                  >
                    <option value="">Select</option>
                    {subjects.map((subject) => (
                      <option value={subject.id}>{subject.subjectName}</option>
                    ))}
                  </select>
                )}
            <div class="input-group-append">
              <button
                onClick={() => handleFilter(selectedFilter)}
                class="btn btn-primary"
                type="button"
              >
                <i class="i-Magnifi-Glass1"></i>
              </button>
            </div>
          </div>
        </div>
        <div className="form-group">
          <label className="text-dark">View By: </label>
          <div className="btn-group ml-1">
            <button
              title="ListView"
              onClick={handleListView}
              className={
                listView
                  ? "btn btn-primary btn-sm"
                  : "btn btn-outline-primary btn-sm"
              }
            >
              <i className="i-Newspaper"></i>
            </button>
            <button
              title="Widgets"
              onClick={handleCardView}
              className={
                cardView
                  ? "btn btn-primary btn-sm"
                  : "btn btn-outline-primary btn-sm"
              }
            >
              <i className="i-Split-Four-Square-Window"></i>
            </button>
          </div>
        </div>
      </div>
      {!loading && (
        <div>
          {cardView && (
            <div>
              {calendarEvents && (
                <div className="row">
                  <div className="col-lg-3 mt-5">
                    <Suspense fallback={<Loading></Loading>}>
                      <Calendar onChange={handleCalendarChange} value={date} />
                    </Suspense>
                  </div>

                  <div className="col-lg-9">
                    {user && (
                      <Suspense fallback={<Loading></Loading>}>
                        <CalendarSchedule
                          calendarEvents={calendarEvents}
                          classrooms={classrooms}
                          user={user.role}
                          token={token}
                          teachers={teachers}
                          dispatch={dispatch}
                          userId={user.uid}
                          courses={courses}
                          resource={selectedRoom}
                          date={date}
                          setDate={setDate}
                        />
                      </Suspense>
                    )}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      )}
      <div>
        {listView && (
          <ScheduleListView
            teachers={teachers}
            classrooms={classrooms}
            classroomSlots={listSlots}
          />
        )}
      </div>
      {loading && (
        <div className="loader-bubble loader-bubble-primary m-5"></div>
      )}
      <Suspense fallback={<Loading></Loading>}>
        <AddClassroomForm
          handleClose={handleClose}
          token={token}
          dispatch={dispatch}
          show={showModal}
        />
      </Suspense>
      <Suspense fallback={<Loading></Loading>}>
        {showCreateSlot && (
          <CreateSlot
            handleShowCreateSlot={handleShowCreateSlot}
            show={showCreateSlot}
            classrooms={classrooms}
            courses={courses}
            teachers={teachers}
            subjects={subjects}
            token={token}
            user={user}
            dispatch={dispatch}
          />
        )}
      </Suspense>
    </div>
  );
};

export default ScheduleList;
